.main-timeline .timeline:before{
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: -8px 0 5px -5px rgba(0, 0, 0, 0.5) inset; */
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}
.main-timeline .timeline-icon{
  width: 210px;
  height: 210px;
  border-radius: 50%;
  border: 25px solid transparent;
  border-top-color: #EE1D25;
  border-right-color: #EE1D25;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  transform: rotate(45deg);
}
.main-timeline .year{
