  font-weight: bold;
  color: #EE1D25;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: rotate(-45deg);
}
.main-timeline .timeline-content{
  width: 35%;
  float: right;
  background: #EE1D25;
  padding: 30px 20px;
  margin: 50px 0;
  z-index: 1;
  position: relative;
  border-radius: 5px!important;
}
.main-timeline .timeline-content:before{
  content: "";
  width: 20%;
  height: 15px;
  background: #EE1D25;
  position: absolute;
  top: 50%;
  left: -20%;
  z-index: -1;
  transform: translateY(-50%);
