.slider__wrapper {
  margin: 0 auto;
}


.slider__content {
  display: flex !important;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  position: relative;
  max-height: 90%;
}
.slider__content .slider__text {
  opacity: 0;
  transition: all 1s ease;
  background: rgba(255, 255, 255, 0.75);
  padding: 2.5em 4em;
  position: absolute;
  left: 0;
  width: 40%;
  z-index: 1;
  opacity: 0;
  transition: all 1s linear;
}
.slider__content .slider__text span {
  font-weight: 600;
  font-size: 1.5em;
  background: linear-gradient(151deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%);
  border-radius: 50%;
  line-height: 35px;
  width: 35px;
  display: block;
  text-align: center;
  color: white;
}
.slider__content .slider__text h3 {
  font-weight: 600;
  font-size: 2em;
  display: inline-block;
  margin-bottom: 0;
}
.slider__content .slider__text h3:after {
  content: "";
  width: 100%;
  height: 3px;
  display: block;
  background: linear-gradient(151deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%);
}
.slider__content .slider__text p {
  line-height: 2;
}
.slider__content .slider__image {
  width: 70%;
  margin: 0;
  position: relative;
}
.slider__content .slider__image:before {
  content: "";
  padding-top: 56.25%;
  display: block;
}
.slider__content .slider__image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  object-fit: cover;
  transition: all 0.75s ease;
}

