/* horizontal timeline */
.main-timeline{
  overflow: hidden;
  position: relative;
}
.main-timeline .timeline{
  position: relative;
  margin-top: -100px;
}
.main-timeline .timeline:first-child{ margin-top: 0; }
.main-timeline .timeline:before,
.main-timeline .timeline:after{
  content: "";
  display: block;
  width: 100%;
  clear: both;
}
.main-timeline .timeline:before{
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: -8px 0 5px -5px rgba(0, 0, 0, 0.5) inset; */
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}
.main-timeline .timeline-icon{
  width: 210px;
  height: 210px;
  border-radius: 50%;
  border: 25px solid transparent;
  border-top-color: #EE1D25;
  border-right-color: #EE1D25;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.main-timeline .year{
  display: block;
  width: 110px;
  height: 110px;
  line-height: 110px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 0 20px rgba(0,0,0,0.4);
  margin: auto;
  font-size: 30px;
  font-weight: bold;
  color: #EE1D25;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.main-timeline .timeline-content{
  width: 35%;
  float: right;
  background: #EE1D25;
  padding: 30px 20px;
  margin: 50px 0;
  z-index: 1;
  position: relative;
  border-radius: 5px!important;
}
.main-timeline .timeline-content:before{
  content: "";
  width: 20%;
  height: 15px;
  background: #EE1D25;
  position: absolute;
  top: 50%;
  left: -20%;
  z-index: -1;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.main-timeline .title{
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  margin: 0 0 10px 0;
}
.main-timeline .description{
  font-size: 16px;
  color: #fff;
  line-height: 24px;
  margin: 0;
}
/* .main-timeline .timeline:nth-child(2n):before{ box-shadow: 8px 0 5px -5px rgba(0, 0, 0, 0.5) inset; } */
.main-timeline .timeline:nth-child(2n) .timeline-icon{ -webkit-transform: rotate(-135deg); transform: rotate(-135deg); }
.main-timeline .timeline:nth-child(2n) .year{ -webkit-transform: rotate(135deg); transform: rotate(135deg); }
.main-timeline .timeline:nth-child(2n) .timeline-content{ float: left; }
.main-timeline .timeline:nth-child(2n) .timeline-content:before{
  left: auto;
  right: -20%;
}
.main-timeline .timeline:nth-child(2n) .timeline-icon{
  border-top-color: #8C1A1C;
  border-right-color: #8C1A1C;
}
.main-timeline .timeline:nth-child(2n) .year{ color: #8C1A1C; }
.main-timeline .timeline:nth-child(2n) .timeline-content,
.main-timeline .timeline:nth-child(2n) .timeline-content:before{ background:#8C1A1C; }
