.main-timeline .timeline:nth-child(3n) .timeline-content,
.main-timeline .timeline:nth-child(3n) .timeline-content:before{ background: #231F20; }
.main-timeline .timeline:nth-child(4n) .timeline-icon{
  border-top-color: #888A8D;
  border-right-color: #888A8D;
}
.main-timeline .timeline:nth-child(4n) .year{ color: #888A8D; }
